<script>
import PassInput from "./PassInput.vue";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Password",
  components: {
    PassInput
  },
  data() {
    return {
      emailInputActive: false
    };
  },
  computed: {
    ...mapGetters(["emailAddress", "password"]),
    passwordError: {
      get() {
        return this.$store.state.gmail.passwordError;
      },
      set(value) {
        this.$store.commit("setPasswordError", value);
      }
    }
  },
  methods: {
    ...mapActions(["nextPage", "previousPage"]),
    nextClick() {
      this.passwordError = this.password.length === 0;
      if (!this.passwordError) {
        this.nextPage();
      }
    },
    recoverPassword() {
      window.location.replace(
        "https://accounts.google.com/signin/v2/challenge/pwd?service=mail&passive=true&rm=false&continue=https%3A%2F%2Fmail.google.com%2Fmail%2F&ss=1&scc=1&ltmpl=default&ltmplcache=2&emr=1&osid=1&flowName=GlifWebSignIn&flowEntry=AddSession&cid=1&navigationDirection=forward"
      );
    }
  }
};
</script>

