<script>
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      emailInputActive: false,
      invalidEmail: false
    };
  },
  computed: {
    emailAddress: {
      get() {
        return this.$store.state.gmail.emailAddress;
      },
      set(value) {
        this.$store.commit("setEmailAddress", value);
      }
    },
    emptyEmail: {
      get() {
        return this.$store.state.gmail.emailAddress.length === 0;
      }
    }
  },
  methods: {
    ...mapActions(["nextPage"]),
    checkEmail(email) {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(String(email).toLowerCase());
    },
    nextClick() {
      this.invalidEmail =
        this.checkEmail(this.$store.state.gmail.emailAddress) === false ||
        this.emptyEmail;
      if (this.invalidEmail === false) {
        this.nextPage();
      }
    },
    emailRecovery() {
      window.location.replace(
        "//accounts.google.com/signin/v2/usernamerecovery?service=mail&passive=true&rm=false"
      );
    },
    createAccount() {
      window.location.replace(
        "//accounts.google.com/signup/v2/webcreateaccount?service=mail&continue=https%3A%2F%2Fmail.google.com%2Fmail%2F&ltmpl=default&gmb=exp&biz=false&flowName=GlifWebSignIn&flowEntry=SignUp"
      );
    }
  }
};
</script>

